exports.push([module.id, "\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\ndiv.sec-map .map {\n  position: absolute;\n  left: 0px;\n  width: 100%;\n  bottom: 0px;\n  border: solid 2px red;\n  top: 0px;\n}\n", "", {"version":3,"sources":["/./client/route-map.vue?57994cfc"],"names":[],"mappings":";;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AA6CA;EACA,mBAAA;EACA,UAAA;EACA,YAAA;EACA,YAAA;EACA,sBAAA;EACA,SAAA;CACA","file":"route-map.vue","sourcesContent":["<template>\n<div class=\"sec-map\">\n  <navi :service=\"service\"></navi>\n  <div class=\"contents-with-nav\">\n    <gmap-map v-ref:gmap class=\"sec-map\" :center=\"{lng: 103.8, lat: 1.38}\" :zoom=\"12\">\n\n      <gmap-marker v-for=\"stop in stops\" track-by='$index' :position=\"stop | stopPosition\" :icon=\"stop | stopIcon $index\" @g-mouseover='selectStop(stop)' @g-mouseout='closeWindow'>\n      </gmap-marker>\n\n      <gmap-infowindow v-if=\"selectedStop != null\" :opened='selectedStop != null' :position=\"selectedStop | stopPosition\">\n        Scheduled: {{selectedStop.time | formatTime}}\n        <div v-if=\"selectedStop.canBoard\">\n          No. of Passengers: {{selectedStop.passengers.length}}\n        </div>\n      </gmap-infowindow>\n\n      <gmap-infowindow v-if=\"selectedPing != null\" :opened=\"selectedPing != null\" :position=\"selectedPing.coordinates | coordinatesToLatLng\">\n        {{selectedPing.time | formatTime}}\n        <br/>\n        <span v-if=\"driversById && driversById[selectedPing.driverId]\">\n                By: <b>{{driversById[selectedPing.driverId].transportCompanies[0].driverCompany.name}}</b>\n              </span>\n      </gmap-infowindow>\n\n      <!-- <ping-line :pings=\"pings\" :options=\"pingOptions\" :sample-rate=\"5\"></ping-line> -->\n\n      <!-- Start and end markers -->\n      <template v-for=\"(driverId,driverPings) in otherPings\">\n        <gmap-marker :position=\"firstPing(driverPings)\"\n          :icon=\"startPoint\" :title=\"Start\">\n        </gmap-marker>\n\n        <gmap-marker :position=\"lastPing(driverPings)\"\n          :icon=\"endPoint\" :title=\"End\">\n        </gmap-marker>\n\n        <ping-line :pings=\"driverPings\" :options=\"otherPingOptions\" :sample-rate=\"5\">\n        </ping-line>\n      </template>\n    </gmap-map>\n  </div>\n</div>\n</template>\n\n<style>\ndiv.sec-map .map {\n  position: absolute;\n  left: 0px;\n  width: 100%;\n  bottom: 0px;\n  border: solid 2px red;\n  top: 0px;\n}\n</style>\n\n<script>\nimport {\n  Map,\n  load,\n  InfoWindow,\n  Polyline,\n  Marker\n} from 'vue-google-maps'\nimport PingLine from './ping-line.vue'\nimport leftPad from 'left-pad'\nimport Vue from 'vue'\nimport _ from 'lodash'\nimport assert from 'assert';\nimport querystring from 'querystring';\n\nvar authAjax = require('./login').authAjax;\nimport {\n  watch\n} from './loading-overlay';\n\nmodule.exports = {\n  components: {\n    'gmap-map': Map,\n    'gmap-marker': Marker,\n    'gmap-polyline': Polyline,\n    'gmap-infowindow': InfoWindow,\n    'ping-line': PingLine,\n  },\n\n  data() {\n    return {\n      title: '',\n      subtitle: '',\n\n      center: {\n        lng: 103.8,\n        lat: 1.38\n      },\n      zoom: 12,\n\n      service: null,\n      trip: {},\n      driversById: {},\n      pings: [],\n      otherPings: {},\n      stops: [],\n      bounds: null,\n\n      selectedStop: null,\n      selectedPing: null,\n    };\n  },\n\n  route: {\n    activate() {\n      this.service = this.$route.params.svc;\n\n      // Trigger map change\n      Vue.nextTick(() => {\n        if (!this.$refs.gmap.mapObject) {\n          return;\n        }\n        this.$broadcast('g-resize-map')\n      });\n    },\n  },\n\n  ready: function() {\n    this.$map = null;\n\n    var queryAgain = () => {\n      this.$queryTimeout = null;\n\n      this.requery()\n        .catch((err) => console.error(err))\n        .then(() => {\n          if (this.$queryTimeout === null) {\n            this.$queryTimeout = setTimeout(queryAgain, 30000);\n          }\n        })\n    };\n    queryAgain();\n  },\n\n  destroyed() {\n    if (this.$queryTimeout) {\n      clearTimeout(this.$queryTimeout);\n    }\n    this.$queryTimeout = false;\n  },\n\n  computed: {\n    startPoint() {\n      return {\n        url: 'img/routeStartMarker.png',\n        size: new google.maps.Size(50, 40),\n        origin: new google.maps.Point(0, 0),\n        anchor: new google.maps.Point(25, 40)\n      };\n    },\n    endPoint() {\n      console.log(\"Testing?\")\n      return {\n        url: 'img/routeEndMarker.png',\n        size: new google.maps.Size(50, 40),\n        origin: new google.maps.Point(0, 0),\n        anchor: new google.maps.Point(25, 40)\n      };\n    },\n    otherPingOptions() {\n      return {\n        polyline: {\n          strokeOpacity: 0.8,\n          strokeWeight: 2\n        }\n      }\n    },\n    pingOptions() {\n      return {\n        polyline: {\n          strokeOpacity: 1.0,\n        }\n      }\n    }\n  },\n\n  filters: {\n    formatTime(sdt) {\n      if (!(sdt instanceof Date)) {\n        sdt = new Date(sdt);\n      }\n      return leftPad(sdt.getHours(), 1, '0') + ':' + leftPad(sdt.getMinutes(), 2, '0');\n    },\n\n    stopIcon(stop, index) {\n      return {\n        url: 'img/stop' +\n          (stop.canBoard ? 'Board' : 'Alight') +\n          (index + 1) + '.png',\n        size: new google.maps.Size(100, 100),\n        origin: new google.maps.Point(0, 0),\n        anchor: new google.maps.Point(20, 20),\n        scaledSize: new google.maps.Size(40, 40),\n      }\n    },\n\n    stopPosition(ts) {\n      return {\n        lat: ts.stop.coordinates.coordinates[1],\n        lng: ts.stop.coordinates.coordinates[0],\n      }\n    },\n    coordinatesToLatLng(cc) {\n      assert(typeof cc.coordinates[1] === 'number');\n      return {\n        lat: cc.coordinates[1],\n        lng: cc.coordinates[0],\n      }\n    }\n  },\n\n\n  watch: {\n    service: function() {\n      watch(this.requery()\n        .then(() => {\n          this.setBounds()\n        }));\n    },\n\n    'trip.transportCompanyId' (companyId) {\n      authAjax(`/companies/${companyId}/drivers`)\n        .then((result) => {\n          this.driversById = _.keyBy(result.json(), 'id')\n        })\n    }\n  },\n\n  methods: {\n    requery: function() {\n      var startTime = new Date();\n      startTime.setHours(0, 0, 0, 0);\n\n      // Pings of other drivers who also\n      // claimed this trip id\n      var pingsPromise = authAjax(`/trips/${this.service}/pingsByTripId?` + querystring.stringify({\n          startTime: startTime.getTime(),\n          limit: 800,\n        }))\n        .then((response) => {\n          this.otherPings = _.groupBy(response.json(), 'driverId');\n        })\n\n      var tripPromise = authAjax(`/trips/${this.service}`)\n        .then(result => result.json());\n      var passengersPromise = authAjax(`/trips/${this.service}/passengers`)\n        .then(result => result.json());\n\n      Promise.all([tripPromise, passengersPromise])\n        .then(([trip, passengerData]) => {\n          var stops = trip.tripStops;\n          var passengers = passengerData;\n\n          var passengersByStopId = _.groupBy(passengers, p => p.boardStopId)\n\n          for (var i = 0; i < stops.length; i++) {\n            stops[i].passengers = passengersByStopId[stops[i].id] || [];\n          }\n\n          this.trip = trip;\n          this.stops = stops;\n        });\n\n      return Promise.all([tripPromise, passengersPromise, pingsPromise]);\n    },\n\n    selectStop(stop) {\n      this.selectedPing = null;\n      this.selectedStop = stop;\n    },\n    closeWindow() {\n      this.selectedPing = null;\n      this.selectedStop = null;\n    },\n\n    setBounds() {\n      var bounds = new google.maps.LatLngBounds();\n      var self = this;\n\n      for (stop of this.stops) {\n        var latlng = new google.maps.LatLng(\n          stop.stop.coordinates.coordinates[1],\n          stop.stop.coordinates.coordinates[0]);\n        bounds.extend(latlng);\n      }\n      this.$refs.gmap.mapObject.fitBounds(bounds);\n    },\n\n    firstPing(pings) {\n      return {\n        lat: pings[pings.length - 1].coordinates.coordinates[1],\n        lng: pings[pings.length - 1].coordinates.coordinates[0],\n      }\n    },\n\n    lastPing(pings) {\n      return {\n        lat: pings[0].coordinates.coordinates[1],\n        lng: pings[0].coordinates.coordinates[0],\n      }\n    }\n  },\n\n  events: {\n    selectPing(ping) {\n      this.selectedPing = ping;\n      this.selectedStop = null\n    }\n  }\n}\n</script>\n"],"sourceRoot":"webpack://"}]);

