nd-color: #FFDDDD;\n}\n.danger-button[_v-7017d689] {\n  background-color: #FF0000;\n  color: #FFFFFF;\n}\n\n", "", {"version":3,"sources":["/./client/route-passengers.vue?f96eb146"],"names":[],"mappings":";;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;;AAyHA;IACA,aAAA;IACA,YAAA;IACA,uBAAA;CACA;AACA;IACA,YAAA;CACA;AACA;IACA,mBAAA;IACA,eAAA;IACA,kBAAA;IACA,WAAA;;IAEA,wBAAA;IACA,uBAAA;CACA;AACA;IACA,0BAAA;IACA,oBAAA;CACA;;AAEA;IACA,0BAAA;CACA;AACA;;IAEA,gBAAA;IACA,0BAAA;IACA,aAAA;CACA;;AAEA;IACA,aAAA;CACA;AACA;EACA,uBAAA;CACA;AACA;EACA,uBAAA;CACA;;AAEA;EACA,cAAA;EACA,eAAA;EACA,kBAAA;EACA,oCAAA;EAAA,4BAAA;EACA,iBAAA;CACA;AACA;EACA,kBAAA;EACA,iBAAA;EACA,oBAAA;EACA,oCAAA;EAAA,4BAAA;CACA;;AAEA;EACA,kBAAA;CACA;AACA;EACA,kBAAA;CACA;;AAEA;IACA,0BAAA;IACA,kBAAA;IACA,aAAA;IACA,eAAA;IACA,YAAA;IACA,oBAAA;IACA,iBAAA;CACA;;AAEA;IACA,0BAAA;CACA;AACA;IACA,0BAAA;CACA;;AAEA;EACA,0BAAA;CACA;AACA;EACA,0BAAA;EACA,eAAA;CACA","file":"route-passengers.vue","sourcesContent":["<template>\n<div>\n    <navi :service=\"tripId\"></navi>\n    <div class=\"contents-with-nav\">\n    <h2>Boarding stops</h2>\n    <table class=\"arrivalInfo\">\n        <tr>\n            <th>Stop number</th>\n            <td v-for=\"tripStop in arrivalInfo\"\n                :class=\"{ boarding: tripStop.canBoard,\n                          alighting: tripStop.canAlight }\"\n                v-show=\"tripStop.canBoard\">\n                {{ $index + 1 }}\n                {{ tripStop.canBoard ? '' : '' }}\n            </td>\n        </tr>\n        <tr>\n            <th>Pax boarding</th>\n            <td v-for=\"tripStop in arrivalInfo\"\n                v-show=\"tripStop.canBoard\">\n                <span v-if=\"tripStop.canBoard\">\n                  {{ tripStop.passengers.length  }}\n                </span>\n            </td>\n        </tr>\n        <tr>\n            <th>Scheduled</th>\n            <td v-for=\"tripStop in arrivalInfo\"\n                v-show=\"tripStop.canBoard\">\n                <span v-if=\"tripStop.canBoard\">\n                {{ tripStop.time | takeLocalTime }}\n            </td>\n        </tr>\n        <tr>\n            <th>Actual</th>\n            <td v-for=\"tripStop in arrivalInfo\"\n                v-show=\"tripStop.canBoard\">\n                <span v-if=\"tripStop.canBoard\">\n                {{ tripStop.bestPing ? tripStop.bestPing.createdAt : '' | takeLocalTime }}\n            </td>\n        </tr>\n        <tr>\n            <th>Diff (mins)</th>\n            <td v-for=\"tripStop in arrivalInfo\"\n                v-show=\"tripStop.canBoard\">\n                <span v-if=\"tripStop.canBoard\">\n                {{ tripStop.bestPing ? tripStop.bestPing.createdAt : '' | minsDiff tripStop.time }}\n            </td>\n        </tr>\n    </table>\n\n\n    <h1>Passenger List</h1>\n    <div v-for=\"stop in arrivalInfo\"\n        v-show=\"stop.canBoard\"\n        track-by=\"id\">\n        <h3 :class=\"{'show-passengers': stop.showPassengers}\"\n            @click=\"togglePassengers(stop)\">({{stop.time | formatTime}}) {{$index + 1}}.   {{stop.stop.description}} - {{stop.stop.road}}</h3>\n        <div v-for=\"passenger in stop.passengers\"\n            :class=\"{passenger: true, 'animate-hide': !stop.showPassengers}\"\n            track-by=\"id\"\n            >\n            {{passenger.index + 1}}.\n            {{passenger.name}}\n            &mdash;\n            {{passenger.telephone}}\n            &mdash;\n            {{passenger.email}}\n        </div>\n    </div>\n\n\n    <h1>Cancel Trip</h1>\n    <form class=\"cancel-form\"\n        method=\"POST\"\n        @submit=\"confirmAndCancel\"\n        >\n        <div v-if=\"trip.status !== 'cancelled'\">\n          <b>Warning</b>: This will cancel the trip, and passengers will be notified\n          via SMS. This action is irreversible.\n\n          <button class=\"danger-button\" type=\"submit\">Cancel Trip</button>\n        </div>\n        <div v-else>\n          This trip has been cancelled.\n        </div>\n    </form>\n\n    <h1>Send message to passengers</h1>\n    <form action=\"/send_message\"\n        method=\"POST\"\n        @submit=\"confirmAndSend\"\n        >\n        <label>\n          Use template:\n          <select v-model=\"sms.message\">\n           <option v-for=\"mt in messageTemplates\"\n              :value=\"mt[1]\"\n              >\n              {{mt[0]}}\n           </option>\n          </select>\n        </label>\n        <input type=\"hidden\" name=\"session_token\" value=\"{{sessionToken}}\" />\n        <input type=\"hidden\" name=\"service\" value=\"{{service}}\" />\n        <textarea v-model=\"sms.message\"\n            style=\"display: block; width: 100%; height: 100px\"\n            name=\"message\"></textarea>\n        <button class=\"message-button\" type=\"submit\">Submit</button>\n    </form>\n    <br/>\n    <br/>\n    <br/>\n    <br/>\n    <br/>\n\n    </div>\n</div>\n</template>\n\n<style scoped>\nform {\n    padding: 1em;\n    width: 100%;\n    border: solid 1px #888;\n}\nlabel select{\n    margin: 1em;\n}\nbutton.message-button {\n    padding: 10px 30px;\n    display: block;\n    margin: 10px auto;\n    width: 80%;\n\n    border: solid 1px black;\n    background-color: #ccc;\n}\ntable.arrivalInfo {\n    border-collapse: collapse;\n    border-spacing: 0px;\n}\n\n.arrivalInfo th {\n    background-color: #ebeff2;\n}\n.arrivalInfo th,\n.arrivalInfo td {\n    min-width: 50px;\n    border: solid 1px #CCCCCC;\n    padding: 5px;\n}\n\n.passenger {\n    padding: 5px;\n}\n.passenger:nth-child(even) {\n  background-color: #FFF;\n}\n.passenger:nth-child(odd) {\n  background-color: #EEE;\n}\n\n.passenger.animate-hide {\n  max-height: 0;\n  padding-top: 0;\n  padding-bottom: 0;\n  transition: 0.1s linear all;\n  overflow: hidden;\n}\n.passenger:not(.animate-hide) {\n  max-height: 100px;\n  padding-top: 5px;\n  padding-bottom: 5px;\n  transition: 0.1s linear all;\n}\n\nh3.show-passengers::before {\n  content: \"[ - ] \";\n}\nh3:not(.show-passengers)::before {\n  content: \"[ + ] \";\n}\n\nh3 {\n    background-color: #ebeff2;\n    margin: 1px 0 0 0;\n    padding: 5px;\n    font-size: 80%;\n    color: #888;\n    white-space: nowrap;\n    overflow-x: auto;\n}\n\ntd.boarding {\n    background-color: #19c3a5;\n}\ntd.alighting:not(.boarding) {\n    background-color: #ff7070;\n}\n\n.cancel-form {\n  background-color: #FFDDDD;\n}\n.danger-button {\n  background-color: #FF0000;\n  color: #FFFFFF;\n}\n\n</style>\n\n<script>\n\nconst leftPad = require('left-pad')\nvar authAjax = require('./login').authAjax;\nvar Vue=require('vue');\nconst _ = require('lodash')\nimport MessageTemplates from './message-templates'\nimport {watch} from './loading-overlay';\n\nVue.filter('formatScheduled', (s) => {\n    return s.substr(0,2) + ':' + s.substr(2,4);\n});\nVue.filter('takeLocalTime', (s) => {\n    if (!s) return '';\n    var now = new Date();\n    var d = new Date(s);\n\n    d = new Date(d.getTime() - 60000 * now.getTimezoneOffset());\n\n    return d.toISOString().substr(11,5);\n});\nVue.filter('minsDiff', (s, sched) => {\n    if (!s) return '';\n    var actualDate = new Date(s);\n    var schedDate = new Date(sched);\n\n    var minsDiff = Math.round((actualDate.getTime() - schedDate.getTime()) / 60000);\n\n    if (minsDiff == 0) {\n        return '0';\n    }\n    else if (minsDiff < 0) {\n        return minsDiff.toFixed(0);\n    }\n    else if (minsDiff > 0) {\n        return '+' + minsDiff.toFixed(0);\n    }\n});\n\n\nmodule.exports = {\n    data () {\n        return {\n            title: '',\n            subtitle: '',\n\n            tripId: null,\n            stops: [],\n\n            ServiceData: window.ServiceData,\n\n            trip: {\n              trip: {\n                tripStops: [],\n              }\n            },\n            passengers: [],\n\n            sms: {\n                message: MessageTemplates[0][1]\n            }\n        };\n    },\n\n    route: {\n        activate() {\n            this.tripId = this.$route.params.svc;\n        },\n    },\n\n    computed: {\n        services() {\n            return this.ServiceData.services;\n        },\n        sessionToken() {\n            return window.localStorage.session_token;\n        },\n        messageTemplates() {\n            return MessageTemplates;\n        },\n\n        setMessage: {\n            set(value) {\n                this.$el.querySelector('textarea[name=\"message\"]')\n\n            },\n        },\n\n        arrivalInfo() {\n          var stops = _.sortBy(this.trip.tripStops, s => s.time);\n          var passengersByStops = _.groupBy(this.passengers, p => p.boardStopId)\n          var index = 0;\n\n          for (let stop of stops) {\n            Vue.set(stop, 'passengers', passengersByStops[stop.id] || [])\n\n            for (let p of stop.passengers) {\n              if (p.name) {\n                try {\n                  var jsonData = JSON.parse(p.name)\n                  p.name = `${jsonData.name} (#${jsonData.index + 1})`\n                  p.telephone = jsonData.telephone\n                  p.email = jsonData.email\n                }\n                catch (err) {\n                }\n              }\n              p.index = index++;\n            }\n          }\n\n          console.log(stops.map(s => _.assign({}, s)))\n          return stops;\n        }\n    },\n\n    watch: {\n        tripId: function () {\n            watch(Promise.all([\n              this.requery(),\n              this.requeryTrips()\n            ]))\n        },\n    },\n\n    ready() {\n      var queryAgain = () => {\n        this.$queryTimeout = null;\n        this.requery()\n        .finally(() => {\n          if (this.$queryTimeout === null) {\n            this.$queryTimeout = setTimeout(queryAgain, 30000);\n          }\n        })\n      };\n\n      queryAgain();\n    },\n\n    destroyed() {\n      if (this.$queryTimeout) {\n        clearTimeout(this.$queryTimeout);\n      }\n      this.$queryTimeout = false;\n    },\n\n    methods: {\n        requeryTrips() {\n            return authAjax(`/monitoring`)\n            .then(result => result.json())\n            .then((status) => {\n              this.trip = _.values(status)\n                  .find(t => t.trip.id == this.tripId)\n                  .trip\n\n              if (this.trip) {\n                for (let tripStop of this.trip.tripStops) {\n                  Vue.set(tripStop, 'showPassengers', true);\n                }\n              }\n            })\n        },\n        requery: function () {\n            return authAjax(`/trips/${this.tripId}/passengers`)\n            .then(result => result.json())\n            .then((passengers) => {\n              this.passengers = passengers\n            })\n            .then(null, (err) => {\n                console.error(err);\n            })\n        },\n\n        confirmAndSend(event) {\n            event.preventDefault()\n\n            if (!confirm(\"Send message?\"))\n                return;\n\n            if (!confirm(\"Really sure?\"))\n                return;\n\n            authAjax(`/trips/${this.tripId}/messagePassengers`, {\n                method: 'POST',\n                body: {\n                    message: this.sms.message,\n                }\n            })\n            .then(() => {\n                alert(\"Message sent!\");\n            })\n            .then(null, (err) => {\n                alert(\"There was an error sending the message\");\n            })\n            return false\n        },\n\n        confirmAndCancel(event) {\n            event.preventDefault()\n\n            var confirmResponse = prompt(\n              `To confirm please enter the route ID (${this.trip.route.id}):`\n            )\n            if (confirmResponse.trim() !== this.trip.route.id.toString()) {\n              alert(\"The trip was not cancelled\");\n              return;\n            }\n\n            watch(authAjax(`/trips/${this.tripId}/statuses?messagePassengers=true`, {\n                method: 'POST',\n                body: {\n                  status: 'cancelled'\n                }\n            })\n            .then(result => result.json())\n            .then(() => {\n                this.requery();\n                alert(\"Trip cancelled! The route list will be updated shortly.\");\n            })\n            .then(null, (err) => {\n                alert(\"There was an error sending the message\");\n            }))\n            return false\n        },\n\n        togglePassengers(stop) {\n          console.log(stop)\n          stop.showPassengers = !stop.showPassengers;\n        }\n    }, /* methods */\n    filters: {\n        formatTime(sdt) {\n            if (!Date.prototype.isPrototypeOf(sdt)) {\n                sdt = new Date(sdt);\n            }\n            return leftPad(sdt.getHours(), 1, '0') + ':' + leftPad(sdt.getMinutes(), 2, '0');\n        },\n    }\n}\n\n</script>\n"],"sourceRoot":"webpack://"}]);

